import React, { Component } from 'react';
import { Link, NavLink } from 'react-router-dom';
// import { Badge, DropdownItem, DropdownMenu, DropdownToggle, Nav, NavItem } from 'reactstrap';
import PropTypes from 'prop-types';

import { AppSidebarToggler,AppBreadcrumb } from '@coreui/react';
import { BrowserRouter, Route } from 'react-router-dom';
import routes from '../../routes';

// import logo from '../../assets/img/brand/logo.svg'
// import sygnet from '../../assets/img/brand/sygnet.svg'

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends Component {

  constructor(props){
    super(props);
    this.signout = this.signout.bind(this);
  }
  render() {

    // eslint-disable-next-line
    const { children, ...attributes } = this.props;

    return (
      <React.Fragment>
        <AppSidebarToggler className="d-lg-none" display="md" mobile />
        <AppSidebarToggler className="d-md-down-none" style={{float:"left"}} display="lg" />
        <span className="bread-crumb-in-header">
          <AppBreadcrumb appRoutes={routes} />
        </span>
        {/* <span class="btn-in-header"> */}
          {this.renderCustomComponent()}
        {/* </span>       */}
      </React.Fragment>
    );
  }

  signout() {
    sessionStorage.removeItem('userId')
  }

  renderCustomComponent() {
    return (
      <div>
        <button className="btn btn-secondary btn-md px-3 mx-3" >Dashboard</button>
        <Link to="/forgot_password" style={{cursor:"pointer"}}>
          <button className="btn btn-secondary btn-md px-3 mx-3" onClick={this.signout}>SIGNOUT</button>
        </Link>
      </div>
    )
  }
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

export default DefaultHeader;
